:root {
  --measure: 60ch;

  --font-plain:Helvetica Neue,Helvetica,Arial,sans-serif;
  --font-special:Barlow Condensed,Helvetica,sans-serif;
  --font-mono:Menlo,Courier,Courier New,Andale Mono,monospace;
  --color-dark:#050505;
  --color-darkish:#404040;
  --color-lightish:#e6e6e6;
  --color-light:#fafafa;
  --color-mid:grey;
  
  --font-size-s: .75rem;
  --font-size: 1rem;
  --font-size-l: 1.75rem;
  --font-size-xl: 2.25rem;


  --heading-size-1: 32px;
  --heading-size: 24px;
  --heading-size-secondary: 19px;
  --text-size: 19px;
  --text-color: #474746;
  --text-color-light: hsl(var(--text-color), 100%, 44%);
  --c-eu: hsla(211.2, 100%, 29.4%, 1);
  --c-eu-5: hsla(211.2, 100%, 29.4%, .5);
  --c-secondary: #FCD838;
  
  --ratio: 1.5;
  --s0: 1rem;
  --s-1: calc(var(--s0) / var(--ratio));
  --s-2: calc(var(--s-1) / var(--ratio));
  --s-3: calc(var(--s-2) / var(--ratio));
  --s-4: calc(var(--s-3) / var(--ratio));
  --s-5: calc(var(--s-4) / var(--ratio));
  --s1: calc(var(--s0) * var(--ratio));
  --s2: calc(var(--s1) * var(--ratio));
  --s3: calc(var(--s2) * var(--ratio));
  --s4: calc(var(--s3) * var(--ratio));
  --s5: calc(var(--s4) * var(--ratio));

  /* UI Colors */
  --primary-hue: 12;
  --color--primary: hsl(var(--primary-hue), 100%, 44%);
  --color--primary--hover: hsl(var(--primary-hue), 100%, 39%);
  --color--primary--active: hsl(var(--primary-hue), 100%, 30%);

  /* Grays */
  --c-g-1: hsl(0, 0%, 10%);
  --c-g-2: hsl(0, 0%, 20%);
  --c-g-3: hsl(0, 0%, 30%);
  --c-g-4: hsl(0, 0%, 40%);
  --c-g-5: hsl(0, 0%, 50%);
  --c-g-6: hsl(0, 0%, 60%);
  --c-g-7: hsl(0, 0%, 70%);
  --c-g-8: hsl(0, 0%, 80%);
  --c-g-9: hsl(0, 0%, 90%);

  /* */
  --border-color: #ebebeb;

  /* Box Shadows */
  --shadow: 0px 4px 4px 0px rgba(37, 37, 37, 0.25);
  --shadow-01: 0px 2px 4px rgba(37, 37, 37, 0.1);
  --shadow-02: 0px 4px 8px rgba(37, 37, 37, 0.1);
  --shadow-03: 0px 8px 16px rgba(37, 37, 37, 0.1);
  --shadow-04: 0px 16px 24px rgba(37, 37, 37, 0.1);
  --shadow-05: 0px 24px 32px rgba(37, 37, 37, 0.1);
}